.experience {
  background: #efe1bd;
}

.timeline {
  display: flex;
  justify-content: center;
  align-items: center;
}

.cardExp {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: #fff;
  border-radius: 0.25em;
  -webkit-box-shadow: 0 3px 0 #ddd;
  box-shadow: 0 3px 0 #ddd;
  padding: 10px;
  margin: 10px;
  width: 50vw;
}

@media only screen and (max-width: 875px) {
  .cardExp {
    width: 100vw;
  }
}

.badge {
  background-color: #ae944f;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  margin-top: 10px;
  margin-bottom: 5px;
  padding: 5px 10px 5px 10px;
}

.badge-name {
  margin: 0px;
  font-size: 11px;
  vertical-align: baseline;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}

.names {
  margin: 0px;
  font-size: 160%;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}

.sub-names {
  margin-bottom: 15px;
  color: #404040;
  font-family: "Raleway", sans-serif;
  font-weight: 200;
}

.sub-sub-names {
  margin: 0px;
  font-size: 70%;
  color: #5c5c5c;
  font-family: "Raleway", sans-serif;
  font-weight: 200;
}

.keywords {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 5px;
}

.keyword-name {
  margin-bottom: 10px;
  margin-right: 5px;
  background-color: #f9f5e9;
  padding: 4px 15px 4px 15px;
  border-radius: 20px;
  font-size: 70%;
  font-family: "Raleway", sans-serif;
  font-weight: 200;
}
