.projects {
  background: #efe1bd;
}

.projects-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 20px;
  margin-left: 20px;
  margin-right: 20px;
}

.project-name {
  margin: 0px;
  padding-top: 3%;
  padding-bottom: 3%;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #000;
  text-align: center;
}

.card-project {
  background: #fff;
  transition: all 0.2s linear;
  z-index: 0;
  cursor: pointer;
  box-shadow: 0 3px 0 #ddd;
  border-radius: 0.25em;
  display: flex;
  flex-direction: column;
  padding: 10px 20px 10px 20px;
  margin: 10px;
  width: 350px;
}

.card-project:hover {
  transform: scale(1.01);
  z-index: 10;
  box-shadow: 0 10px 20px rgb(0 0 0 / 70%);
}

.project-title {
  margin-top: 5%;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #000;
  text-align: center;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}

.project-subTitle {
  margin-top: 20px;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #000;
  text-align: center;
  font-size: 12px;
  font-family: "Raleway", sans-serif;
  font-weight: 200;
}

.project-image {
  height: 240px;
  width: 320px;
}
